import React, { Component } from 'react'
import Home from './Home.js'
import Orders from './Orders.js'
import Product from './Product.js'
import './scss/App.scss'
import {BrowserRouter, Route, Link, Switch, Redirect, NavLink} from 'react-router-dom'

class App extends Component {
  render () {
    return (
      <BrowserRouter>
        <div>
          <header>
            <h1 className='homeTitle'><Link to='/'>Shoptime</Link></h1>
            <ul>
              <li><NavLink to='/Home' activeClassName='selected'>HOME</NavLink></li>
              <li><NavLink to='/Orders' activeClassName='selected'>ORDERS</NavLink></li>
              <li><NavLink to='/Product' activeClassName='selected'>PRODUCT</NavLink></li>
            </ul>
            <span className='admin'>Admin</span>
          </header>
          <Switch>
            <Route path='/Home' component={Home} />
            <Route path='/Orders' component={Orders} />
            <Route path='/Product' component={Product} />
            <Redirect to='/Home' />
          </Switch>
        </div>
      </BrowserRouter>
    )
  }
}

export default App



