import React, { Component } from 'react'
import './scss/Home.scss'
import chart from './chart.png'
import man1 from './man1.jpg'
import man2 from './man2.jpeg'
import man3 from './man3.jpg'

class Home extends Component {
  render () {
    return (
      <div className='home'>
        <div className='dashboardTitle'>
          <h2>OVERVIEW</h2>
          <span>2018/6/6 <i className='fas fa-caret-right' /> 2018/6/13
             <select id='dateSelect'>
               <option value=''>Weekly</option>
               <option value=''>Monthly</option>
               <option value=''>Yearly</option>
             </select>
          </span>
        </div>
        <div className='dataInfo'>
          <div className='totalRevenue'>
            <i class='fas fa-hand-holding-usd'> TOTAL REVENUE</i>
            <p className='dataNumber'>54,540</p>
          </div>
          <div className='totalCost'>
            <i class='fas fa-boxes'> TOTAL REVENUE</i>
            <p className='dataNumber'>12,660</p>
          </div>
          <div className='netIncome'>
            <i class='fas fa-money-bill'> NET INCOME</i>
            <p className='dataNumber'>41,880</p>
          </div>
        </div>
        <img className='chart' src={chart} />
        <div className='websiteAndOrder'>
          <div className='transactionWebsite'>
            <p>Transaction Website</p>
            <ul>
              <li>
                <i class='fab fa-facebook-square' />
                <span className='domain'>Facebook.com</span>
                <span className='dataFlow'>123,456</span>
                <span className='status green'><i class='fas fa-arrow-up' /> 15%</span>
              </li>
              <li>
                <i class='fab fa-google' />
                <span className='domain'>Facebook.com</span>
                <span className='dataFlow'>123,456</span>
                <span className='status green'><i class='fas fa-arrow-up' /> 15%</span>
              </li>
              <li>
                <i class='fab fa-weibo' />
                <span className='domain'>Facebook.com</span>
                <span className='dataFlow'>123,456</span>
                <span className='status red'><i class='fas fa-arrow-down' /> 15%</span>
              </li>
              <li>
                <i class='fab fa-wordpress' />
                <span className='domain'>Facebook.com</span>
                <span className='dataFlow'>123,456</span>
                <span className='status red'><i class='fas fa-arrow-down' /> 15%</span>
              </li>
            </ul>
          </div>
          <div className='latestOrders'>
            <p>Latest Orders</p>
            <ul>
              <li>
                <img src={man1} />
                <div className='product'>
                  <p className='productName'>Vintage T-shirt</p>
                  <p className='postTime'><i class='far fa-clock' /> 2018/6/13  13:42</p>
                  <p className='uploader'><i class='fas fa-male' /> Belle Willis</p>
                </div>
                <div className='clickRate'>
                  <p className='total'>Total</p>
                  <p className='quantity'>3,200</p>
                </div>
              </li>
              <li>
                <img src={man2} />
                <div className='product'>
                  <p className='productName'>Cowboy Jacket</p>
                  <p className='postTime'><i class='far fa-clock' /> 2018/6/13  10:45</p>
                  <p className='uploader'><i class='fas fa-male' /> Adrian Cummings</p>
                </div>
                <div className='clickRate'>
                  <p className='total'>Total</p>
                  <p className='quantity'>2,800</p>
                </div>
              </li>
              <li>
                <img src={man3} />
                <div className='product'>
                  <p className='productName'>Coach Coat</p>
                  <p className='postTime'><i class='far fa-clock' /> 2018/6/13  8:26</p>
                  <p className='uploader'><i class='fas fa-male' /> Lura Holland</p>
                </div>
                <div className='clickRate'>
                  <p className='total'>Total</p>
                  <p className='quantity'>1,600</p>
                </div>
              </li>
            </ul>

          </div>
        </div>
      </div>
    )
  }
}

export default Home



